img {
  max-width: 100%;
  width: 300px;
  display: block;
}

.wrapper.is-hidden {
  opacity: 0;
  pointer-events: none;
  display: none;
}

.container {
  flex-direction: column;
  gap: 50px;
  display: flex;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 2px solid #ff3d00;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 12px;
  height: 12px;
  border: 2px solid #fff;
  border-radius: 50%;
  position: absolute;
  top: 4px;
  left: 4px;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.wrapper {
  width: 100vw;
  height: 100vh;
  z-index: 100;
  background-color: rgba(0, 0, 0, .6);
  justify-content: center;
  align-items: center;
  transition: opacity .5s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

